/**
 * SpinnerAnimation.js
 * Spinner animation for loading
 *
 * @version 0.0.1
 * @author [Wilson](wilson@policypal.com)
 */
import React, { Component } from 'react';
import PropTypes from 'prop-types';

import './SpinnerAnimation.scss';

class SpinnerAnimation extends Component {
  render() {
    const { dotsColor } = this.props;

    return (
      <div className="spinner-animation">
        <div className="dot1" style={{ backgroundColor: dotsColor }} />
        <div className="dot2" style={{ backgroundColor: dotsColor }} />
      </div>
    );
  }
}

SpinnerAnimation.defaultProps = {
  dotsColor: '#333',
};

SpinnerAnimation.propTypes = {
  dotsColor: PropTypes.string,
};

export default SpinnerAnimation;
