/**
 * Button.js
 * Custom Button
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
import React, { Component } from 'react';
import PropTypes from 'prop-types';

import SpinnerAnimation from '../../Templates/SpinnerAnimation/SpinnerAnimation';
import './Button.scss';

/**
 * Custom Button class
 */
class Button extends Component {
  constructor(props) {
    super(props);

    this.state = {
      variantStyle: this.getVariantClassString(),
    };
  }

  componentDidUpdate(prevProps) {
    const { variant } = this.props;
    if (variant !== prevProps.variant) {
      this.setState({
        variantStyle: this.getVariantClassString(),
      });
    }
  }

  getVariantClassString() {
    const { variant } = this.props;

    // Default to medium color
    const classString = variant ? `button-${variant}` : 'button-medium-color';

    return classString;
  }

  render() {
    const { variantStyle } = this.state;

    const {
      onClick,
      type,
      children,
      disabled,
      width,
      height,
      className,
      isLoading,
    } = this.props;

    const disabledStyle = disabled ? 'disabled' : '';
    const newWidth = width ? `${width}px` : '101%';
    const newHeight = height ? `${height}px` : '100%';

    return (
      <button
        type={type}
        className={`${className} ${variantStyle} ${disabledStyle}`}
        onClick={() => { if (isLoading) return; onClick(); }}
        tabIndex={0}
        onKeyUp={() => { if (isLoading) return; onClick(); }}
        disabled={disabled}
        style={{ width: newWidth, minHeight: newHeight }}
      >
        {
          isLoading ?
            <SpinnerAnimation />
          :
           children
        }
      </button>
    );
  }
}

Button.defaultProps = {
  type: 'button',
  onClick: () => {},
  disabled: false,
  width: '',
  height: '',
  className: '',
  isLoading: false,
};


Button.propTypes = {
  type: PropTypes.string,
  onClick: PropTypes.func,
  variant: PropTypes.string.isRequired,
  disabled: PropTypes.bool,
  width: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.number,
  ]),
  height: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.number,
  ]),
  className: PropTypes.string,
  isLoading: PropTypes.bool,
};

export default Button;
