import _ from 'lodash';
import { withRouter } from 'react-router-dom';
import React, { Component } from 'react';
import { Col, Row } from 'reactstrap';
import PropTypes from 'prop-types';
import { LazyLoadImage } from 'react-lazy-load-image-component';

import ReactSlickCarousel from 'components/Templates/ReactSlickCarousel/ReactSlickCarousel';
import Button from 'components/FormsV3/Button/Button';

// import 'react-lazy-load-image-component/src/effects/opacity.css';
import './LandingSplash.scss';

import { splashData } from '../Landing.constants';

class LandingSplash extends Component {
  renderType1(data, index) {
    const { history } = this.props;
    return (
      <div
        key={`landing-splash-${index}`}
        className="landing-splash-slide-container"
      >
        <Row className="landing-splash-slide-inner" noGutters>
          <Col className="d-none d-lg-block" lg={2} />
          <Col className="desc-container" xs={12} lg={4}>
            <h2>{data.title}</h2>
            <p>{data.description}</p>
            <Button
              variant="medium-color"
              width={225}
              height={60}
              onClick={() => {
                if (data.route) { history.push(data.route); }
              }}
            >
              <p>{data.button}</p>
            </Button>
          </Col>
          <Col className="splash-img-container" xs={12} lg={6}>
            {
              data.route
                ? (
                  <button
                    type="button"
                    onClick={() => history.push(data.route)}
                  >
                    <div className="splash-img">
                      <LazyLoadImage
                        alt={data.alt}
                        src={data.src}
                        // effect="opacity"
                      />
                    </div>
                  </button>
                ) : (
                  <div className="splash-img">
                    <LazyLoadImage
                      alt={data.alt}
                      src={data.src}
                      // effect="opacity"
                    />
                  </div>
                )
            }
          </Col>
          {/* <Col className="d-none d-lg-block" lg={1} /> */}
        </Row>
      </div>
    );
  }

  renderType2(data, index) {
    const { history } = this.props;
    return (
      <div
        key={`landing-splash-${index}`}
        className="landing-splash-slide-container type-2"
      >
        <Row className="landing-splash-slide-inner" noGutters>
          <Col className="d-none d-lg-block" lg={2} />
          <Col xs={12} lg={10}>
            <Row noGutters>
              <Col className="splash-img-container" xs={{ size: 12, order: 2 }} lg={{ size: 12, order: 1 }}>
                <div className="splash-img">
                  <LazyLoadImage
                    alt={data.alt}
                    src={data.src}
                    // effect="opacity"
                  />
                </div>
              </Col>
              <Col className="desc-container" xs={{ size: 12, order: 1 }} lg={{ size: 12, order: 2 }}>
                <h2>{data.title}</h2>
                <p>{data.description}</p>
                <Row className="button-container" noGutters>
                  <Col className="button-col" xs={6}>
                    <Button
                      variant="medium-outline-inverse"
                      width={165}
                      height={60}
                      onClick={() => {
                        if (data.route2) { history.push(data.route2); }
                      }}
                    >
                      <p>{data.button2}</p>
                    </Button>
                  </Col>
                  <Col className="button-col" xs={6}>
                    <Button
                      variant="medium-color"
                      width={165}
                      height={60}
                      onClick={() => {
                        if (data.route) { history.push(data.route); }
                      }}
                    >
                      <p>{data.button}</p>
                    </Button>
                  </Col>
                </Row>
              </Col>
            </Row>
          </Col>
        </Row>
      </div>
    );
  }

  renderSplash(data, index) {
    return (
      <div
        key={`landing-splash-${index}`}
        className="landing-splash-slide-container type-3"
      >
        <Row className="landing-splash-slide-inner" noGutters>
          <Col className="d-none d-lg-block" lg={2} />
          <Col xs={12} lg={10}>
            <div className="splash-img">
              <LazyLoadImage
                alt={data.alt}
                src={data.src}
                // effect="opacity"
              />
            </div>
            <h1>Insurance just got <span className="easier"> easier</span></h1>
          </Col>
        </Row>
      </div>
    );
  }

  renderSlide(data, index) {
    switch (data.renderType) {
      case 1: return this.renderType1(data, index);
      case 2: return this.renderType2(data, index);
      default: return this.renderSplash(data, index);
    }
  }

  render() {
    return (
      <div className="landing-splash-container">
        <ReactSlickCarousel
          lazyLoad
          dots
          className="green-dots"
          slidesToShow={1}
          autoplay
          autoplaySpeed={5000}
        >
          {_.map(splashData, (data, index) => this.renderSlide(data, index))}
        </ReactSlickCarousel>
      </div>
    );
  }
}

LandingSplash.defaultProps = {
};

LandingSplash.propTypes = {
  history: PropTypes.object.isRequired,
};

export default withRouter(LandingSplash);
